<template>
    <b-modal id="dialog-company" centered :title="title" hide-footer>
      <b-form @submit="onSubmit" @reset="onReset" v-if="show">
        <b-form-group
          label="Email address:"
        >
          <b-form-input
            v-model="form.companyName"
            required
            placeholder="Masukkan nama Perusahaan"
          ></b-form-input>
          <b-form-invalid-feedback :state="nameValidation">
            Your user ID must be 5-12 characters long.
          </b-form-invalid-feedback>
          <b-form-valid-feedback :state="nameValidation">
            Looks Good.
          </b-form-valid-feedback>
        </b-form-group>

        <b-form-group
          label="Email address:"
          description="We'll never share your email with anyone else."
        >
          <b-form-input
            v-model="form.companyEmail"
            type="email"
            required
            placeholder="Enter email"
          ></b-form-input>
          <b-form-invalid-feedback :state="emailValidation">
            Your user ID must be 5-12 characters long.
          </b-form-invalid-feedback>
          <b-form-valid-feedback :state="emailValidation">
            Looks Good.
          </b-form-valid-feedback>
        </b-form-group>

        <b-form-group label="Address:">
          <b-form-input
            v-model="form.companyAddress"
            required
            placeholder="Enter Address"
          ></b-form-input>
          <b-form-invalid-feedback :state="addressValidation">
            Your user ID must be 5-12 characters long.
          </b-form-invalid-feedback>
          <b-form-valid-feedback :state="addressValidation">
            Looks Good.
          </b-form-valid-feedback>
        </b-form-group>

        <b-form-group label="Course:" >
          <b-form-select
            v-model="form.companyCourse"
            :options="courses"
            required
            placeholder="Select Course"
          ></b-form-select>
            <b-form-invalid-feedback :state="validation">
              Your user ID must be 5-12 characters long.
            </b-form-invalid-feedback>
            <b-form-valid-feedback :state="validation">
              Looks Good.
            </b-form-valid-feedback>
        </b-form-group>
        <b-button type="submit" variant="primary">Submit</b-button>
        <b-button type="reset" variant="danger">Cancel</b-button>
      </b-form>
    </b-modal>
</template>

<script>
  export default {
    props:[ 'dialog', 'title'],
    data() {
      return {
        form: {
          email: '',
          name: '',
          food: null,
          checked: []
        },
        courses: [
          'Python Dasar', 'Python Menengah', 'Python Tingkat Lanjut'
        ],
        show: true
      }
    },
    methods: {
      openDialog () {
         this.$bvModal.show('dialog-company')
      },
      onSubmit(evt) {
        evt.preventDefault()
        alert(JSON.stringify(this.form))
      },
      onReset(evt) {
        evt.preventDefault()
        // Reset our form values
        this.form.email = ''
        this.form.name = ''
        this.form.food = null
        this.form.checked = []
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$bvModal.hide('dialog-company')
        this.$nextTick(() => {
          this.show = true
        })
      }
    }
  }
</script>